import React , { Component } from "react";
import Person from "./person/Person";
class Persons extends Component {
  static getDrivedStateFromeProps(props,state){
    console.log('[persons.js] getDrivedStateFromeProps')
    return state
  }
  shouldComponentUpdate(nextProps,nestState){
    console.log('[persons.js] shouldComponentUpdate')
    return true
  }
  getSnapshotBeforUpdate(prevProps,prevState){
    console.log('[persons.js] get snapshot before update')
    return null
  }
  componentDidUpdate(){
    console.log('[persons.js] component did update')
  }
  render(){
    console.log('[persons.js] rendering...')
    return this.props.persons.map((person , index) => {
      return <Person 
      name={person.name} 
      age={person.age}
      key={person.id}
      Click={this.props.clicked.bind(this, index)}
      Change={this.props.changed.bind(this, person.id)}
      ></Person>
    })
  }
  }
// btnstyle.push(styled.red)
export default Persons