import './App.css';
import styled from './App.module.css';
import Persons from "../components/persons";
import React , { Component } from 'react';
import Validation from "../components/validation";
import Char from "../components/Char"
import HeadSection from "../components/headSection"

class App extends Component {
  constructor(props) {
    super(props);
    console.log('constructer')
    this.state = {
      persons : [ 
        {id:'fasde',name:"max",age:27},
        {id:'asde',name:"manu",age:17},
        {id:'cgfrfd',name:"alex",age:21}
      ],
      showPersons: false,
      userInput: ""
    }
    
  }
  static getDrivedStateFromProps(props, state){
    console.log('getDrivedStateFromProps '+ props)
    return state
  }
  componentDidMount(){
    console.log('componentDidMount')
  }

  togglePersonsHandler = () => {
    if(this.state.persons.length===0){
      return
    }
    this.setState({
      showPersons: !this.state.showPersons
    })
  }
  nameChangeHandler = (id , event) => {
    const personIndex = this.state.persons.findIndex(p => {
      return p.id === id;
    })
    const person = {...this.state.persons[personIndex]}
    person.name = event.target.value;
    const persons = [...this.state.persons];
    persons[personIndex] = person;
    this.setState({persons : persons})
  }
  deletePersonHandler = (personIndex) => {
    const persons = [...this.state.persons];
    persons.splice(personIndex, 1);
    this.setState({
      persons: persons
    })
  }
  inpChangeHandler = (event) => {
    this.setState({userInput: event.target.value})
  }
  deleteCharHandler = (index) => {
    const input = [...this.state.userInput]
    input.splice(index,1)
    this.setState({userInput:input.join('')  })
  }
  render(){
    console.log('render')
    const hrStyle = {
      width: '99%'
    }
    let persons = null;
    if(this.state.showPersons){
      persons = (
        <Persons 
        persons={this.state.persons}
        clicked={this.deletePersonHandler}
        changed={this.nameChangeHandler}
        />
      )
    }
    const charList = this.state.userInput.split('').map((ch,i)=>{
      return <Char character={ch} CharHandler={this.deleteCharHandler.bind(this,i)}/>
    })
    return (
      <div className={styled.App}>
        <header className="App-header">
          <HeadSection persons={this.state.persons} clicked={this.togglePersonsHandler} showPerson={this.state.showPersons} />
          {persons}
          <hr style={hrStyle} />
          <input type="text" onChange={this.inpChangeHandler} value={this.state.userInput}/>
          <p>{this.state.userInput}</p>
          <Validation inputLength={this.state.userInput.length}/>
          <div>
          {charList}
          </div>
          
          
          <a
            className="App-link"
            href="https://reactjs.org"
            target="_blank"
            rel="noopener noreferrer"
          >
            Learn react
          </a>
        </header>
      </div>
    );
  }
}

export default App;
