import React from "react";
import logo from '../assets/logo.svg';
import classes from "./headsection.module.css";
const HeadSection = (props) => {
  const assingedClasses = [];
  let personText = [props.persons.length,'s'];
  switch(props.persons.length){
    case 0:
      personText[0] = 'no'
    case 1:
      personText[1] = ''
      assingedClasses.push(classes.bold)
    case 2:
      assingedClasses.push(classes.red)
    break;
    default:
      personText = [props.persons.length,'s']
    break;
  }
  return(
    <div className={classes.HeadSection}>
      <img src={logo} className="App-logo" alt="logo" />
      <p className={assingedClasses.join(' ')}>
        create react App with {personText[0]} person{personText[1]}
      </p>
      <button className={props.showPerson?classes.red:null} onClick={props.clicked}>show persons</button>
    </div>
  );
}
export default HeadSection